.task {
	display: flex;
	width: 100%;
	margin: 0px 0px 15px 0px;
	padding-bottom: 10px;
	border-bottom: #dadada 1px solid;

	// .task__data
	&__data {
		width: 100%;
		@media (min-width: 991.98px) {
			cursor: pointer;
		}
	}

	&__head {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	// .task__name
	&__name {
		width: 83%;
		font-size: 16px;
		font-weight: 400;
		color: #4d4d4d;
		margin: 0px 0px 4px 0px;
	}
	// .task__description
	&__description {
		font-size: 14px;
		line-height: 16 /14 * 100%;
		font-weight: 300;
		color: #8b8b8b;

		margin: 0px 0px 10px 0px;
	}

	&__footer {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	// .task__date {
	&__date {
		display: flex;
		align-items: center;
	}

	// .task__from
	&__from {
		font-size: 12px;
		line-height: 16 / 14 * 100%;
		overflow: visible;
		width: 40%;
		color: #8b8b8b;
		text-align: right;
		@media (max-width: 479.98px) {
			width: 30%;
		}
		-webkit-touch-callout: none; /* iOS Safari */
		-webkit-user-select: none; /* Chrome/Safari/Opera */
		-khtml-user-select: none; /* Konqueror */
		-moz-user-select: none; /* Firefox */
		-ms-user-select: none; /* Internet Explorer/Edge */
		user-select: none;
	}
}

.expired {
	background-color: #ef3a3a;
	width: fit-content;
	color: white;
	padding: 5px;
	line-height: 14 /12 * 100%;
	font-size: 12px;
	border-radius: 4px;
	font-weight: 600;
	transform: translateY(-5px);
}
