.input {
	background-color: #fff;

	// .input__header
	&__header {
		display: block;
		font-weight: 500;
		color: #000;
		font-size: 12px;
		line-height: 15 / 12 * 100%;
		margin: 0px 0px 6px 0px;
		@media (min-width: 991.98px) {
			cursor: text;
		}
	}

	input {
		padding: 0px 6px 0px 6px;
		border: 0.5px solid #8b8b8b;
		border-radius: 5px;
		outline: none;
		height: 26px;
	}

	// .input__sizeBig
	&__sizeBig {
		width: 350px;

		@media (max-width: 365px) {
			width: 310px;
		}
	}

	// .input__sizeMed
	&__sizeMed {
		width: 259px;
		@media (max-width: 365px) {
			width: 219px;
		}
	}
}
